<template>
  <transition name="fade">
    <div class="singerPage">
      <header
        class="songList-header"
        ref="header"
        :style="{'background-image':`url(${singerData.picUrl})`}"
      >
        <!-- :class="{anop:anop}" -->
        <i
          class="iconfont icon-back"
          @click="back"
        />
        <p>{{ singerData.singerName }}</p>
      </header>
      <section
        class="songList-container"
        ref="container"
      >
        <!-- {{id}} -->
      </section>
      <!-- <ul class="item-tab" :style='sty'>
             <li   :class="{active:cType==0}" @click="cType=0">单曲</li>
             <li  :class="{active:cType==1}" @click="cType=1">专辑</li>
             <li>视频</li>
        </ul> -->
      <div
        class="music-list-m"
        ref="list"
      >
        <div
          class="music-list-h"
          :style="sty"
          @click="playMusic(0)"
          v-show="musicData!=''&&cType==0"
        >
          <!-- :style='sty' -->
          <i class="iconfont icon-start" />
          <p>全部播放<span /></p>
        </div>
        <!-- <ul class="music-list">
              <li class="music-list-item " v-for="(item, index) in musicData" :key="item.index" @click="playMusic(index)" :class="{'active':getCurPlayMusicInfo.mid == item.musicData.songmid}" >
                <p class="index">{{index+1}}</p>
                <div class="content">
                    <p class="name">{{item.musicData.songname}}</p>
                    <p class="singer"><span v-for="singer in item.musicData.singer" :key="singer.mid">{{singer.name}}<span>/</span></span><span>{{item.musicData.albumname}}.{{item.musicData.albumdesc}}</span></p>
                </div>
              </li>
            </ul> -->
            
        <MusicList
          :musiclist="musicData"
          v-show="cType==0"
        />
        <Album v-show="cType==1" />
      </div>
      <div class="bg-mask" />
      <div
        class="bg-mask-play"
        :style="{'background-image':`url(${singerData.picUrl})`}"
      />
    </div>
  </transition>
</template>

<script>
import {singerSongListAPI} from '@/api'
import {mapActions,mapMutations,mapState, mapGetters} from 'vuex'
import MusicList from '@/components/musicList'
import Album from '@/components/Album'
import { Indicator } from 'mint-ui'
export default {
  name: 'Singer',
  data() {
      return {
          singerData:{},
          id:'',
          musicData:[],
          anop:false,
          anob:false,
          cIndex :-1,
           sty:{
                position: '',
                top:''
            },
          cType:0
      }
  },
  props:['musiclist'],
  components:{
    MusicList,Album
  },
  computed: {
       ...mapState('playList',['songList','index','start']), //播放列表歌曲
       ...mapGetters('playList',['getCurPlayMusicInfo'])
  },
  methods: {
      back(){
        this.$router.go(-1)
      },
      getSingerData(){
          let picUrl = `http://y.gtimg.cn/music/photo_new/T001R300x300M000${this.$route.params.id}.webp`
          this.$set(this.singerData,'picUrl',picUrl)
          this.$set(this.singerData,'singerName', this.$route.query.singerName)
          this.id = this.$route.params.id;
        // this.singerData.picUrl = this.$route.query.picUrl;
        // this.singerData.singerName = this.$route.query.singerName;
       
       // console.log(this.singerData)
         Indicator.open('加载中...');
          this.musicData = [],
          this.axios.get(`${singerSongListAPI}?id=${this.id}`)  
            .then(res=>{
                this.musicData = res.data.data;
                 Indicator.close();
                //console.log(this.musicData)
            })
      },
        ...mapMutations('playList',['setPlayList']),
          playMusic(index){ //播放音乐，更新store中的播放列表，和序号
                //console.log(song)
            // alert(111)
            if (index == this.cIndex && this.start) {//单曲点击播放的歌曲和上次一样
                return;
            }
            this.cIndex = index;
            let songList = this.musicData;
            if (songList.length>1) {
            //  console.log(songList)
                this.setPlayList({songList,index})
            }
        },
  },
    mounted() {
         this.getSingerData();
         window.addEventListener('scroll',()=>{
            if (window.scrollY >= this.$refs.container.offsetHeight) {
                    this.sty.position = 'fixed';
                    this.sty.top = this.$refs.header.offsetHeight + 'px';
            }else{
                this.sty.position = '';
                this.sty.top = '';
            }
        })
    },
   watch: {
    '$route': function(to,from){
      if (to.name == 'singer') {
        if (to.params.id != this.id) {
            this.getSingerData();
          //this.getSongListDetail();
        } else{
         
        }
      }
    }
  }, 
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.singerPage{
  position: absolute;
  top:0;
  bottom: 0;
  width: 100%;
  z-index: 10;
  .songList-header{
    height: 80px;
    position: fixed;
    width: 100%;
    z-index:15;
    // background-repeat: no-repeat;
    // background-size: cover;
    // background-position: 50%;
    background-repeat: no-repeat;
    background-size: 100%;
    opacity:1;
    transition: opacity 1s;
    line-height: 80px;
    text-align: center;
    .iconfont{
      color: #000;
      display: block;
      padding: 0 30px;
      font-size: 30px;
      position: absolute;
    }
    p{
      color: #000;
     font-size: $primary-font-size;
    }
  }
  .anop{
     opacity:1;
  }
  .songList-container{
    height: 355px;
    position: relative;
    top: 80px;
     z-index: 1;
  }
  .bg-mask{
    position: absolute;
    top: 0;
    height: 435px;
    background: #292a2b;
    width: 100%;
  }
  .bg-mask-play{
    position: fixed;
    top: 0;
     height: 435px;
    width: 100%;
    background-repeat: no-repeat;
    background-size: 100%;
    opacity:1;
    transform: translateZ(0);
    z-index: 9;
   // background-image: url('http://p.qpic.cn/music_cover/ESQkDB4ohgibFpRT23xHVFZLHzuRNNmsRYsmlOSRl5MZ6vWaHXU23vw/300?n=1')
  }
   .music-list-m{
    position: absolute;
    top: 435px;
    bottom: 110px;
    width: 100%;
    background: #fff;
    z-index: 11;
  }
  .music-list-h{
    height: 100px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-bottom: 1px solid #eee;
    width: 100%;
    background: #fff;
    .iconfont{
      color: $primary-color;
      font-size: 40px;
      width:100px;
      text-align: center;
    }
    p{
      color: #000;
      font-size: $primary-font-size;
    }

  }

  
  .item-tab{
      position: absolute;
      top:435px;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      background: #fff;
      z-index:15;
      .active{
          color: $primary-color;
          border-bottom: 5px solid $primary-color ;
      }
      li{
          flex: 1;
          height: 85px;
          line-height: 85px;
          cursor: pointer;
          box-sizing: border-box;
          color: $light-text-color;
          font-size: $primary-font-size;
         
      }
  }
}
</style>
