<template>
  <div class="musicList">
    <div class="music-list-m">
      <ul class="music-list"  v-for="(item, index) in musiclist"
          :key="item.mid">
        <li v-if="item.id"
          class="music-list-item "
          @click="playMusic(index)"
          :class="{'active':getCurPlayMusicInfo.mid == item.mid}"
        >
          <template >
            <p class="index">
              {{ index+1 }}
            </p>
            <div class="content">
              <p class="name">
                {{ item.name }}
              </p>
              <p class="singer">
                <span
                  v-for="singer in item.singer"
                  :key="singer.mid"
                >{{ singer.name }}<span>/</span></span><span v-if="item.album">{{ item.album.title||'空' }}</span>
              </p>
            </div>
          </template>
        </li>
        
        <li 
          class="music-list-item "
          v-else
          @click="playMusic(index)"
          :class="{'active': getCurPlayMusicInfo.mid == item.musicData.songmid}"
        >
          <template>
            <p class="index">
              {{ index+1 }}
            </p>
            <div class="content">
              <p class="name">
                {{ item.musicData.songname }}
              </p>
              <p class="singer">
                <span
                  v-for="singer in item.musicData.singer"
                  :key="singer.mid"
                >{{ singer.name }}<span>/</span></span><span>{{ item.musicData.albumname }}.{{ item.musicData.albumdesc }}</span>
              </p>
            </div>
          </template>
        </li>

      </ul>
      
    </div>
  </div>
</template>
<script>
import {mapGetters,mapMutations} from 'vuex'

export default {
  name: 'MusicList',
  props:{
    musiclist:{
        type :Array
    }
  },
  computed: {
      ...mapGetters('playList',['getCurPlayMusicInfo'])
  },
  data() {
      return {
          cIndex : -1,
      }
  },
  methods: {
      ...mapMutations('playList',['setPlayList']),
      playMusic(index){ //播放音乐，更新store中的播放列表，和序号
        if (index == this.cIndex && this.start) {//单曲点击播放的歌曲和上次一样
            return;
        }
        //console.log(index)
       // console.log(this.songListData)
        this.cIndex = index;
        let songList = this.musiclist
        if (songList.length>1) {
            this.setPlayList({songList,index})
        }
    },
  },
    mounted() {
        //console.log(this.musiclist)
    },
    updated() {
     // console.log(this.musiclist)
    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .music-list-m{
      .music-list{
        box-sizing: border-box;
        .music-list-item{
          display: flex;
          justify-content: flex-start;
          align-items: center;
          height: 100px;
          border-bottom: 1px solid #eee;
          color: #000;
          font-size: $primary-font-size;
          cursor: pointer;
          background: #fff;
          .index{
            width:100px;
            text-align: center;
          }
          .name{
            width: 600px;
            @include text-ellipsis-one;
          }
          .singer{
            color:$light-text-color;
            font-size: $small-font-size;
            width: 600px;
            @include text-ellipsis-one;
          }
        }
        .active{
          color: $primary-color !important;
          border-left:10px solid $primary-color;
          .singer{
            color:$primary-color  !important;
          }
        }
      }
     
  }
  
</style>
